















import { Component, Vue } from "vue-property-decorator";
import Message from "../components/Message.vue";
@Component({
  components: {
    Message
  }
})
export default class Detail extends Vue {
  public detail!: number;
  public heroName: string = "";
  public id!: number;
  public goBack(): void {
    this.$router.go(-1);
  }
  public save(): void {
    this.$store.commit('editHero',{name:this.heroName,id:this.id})
    this.$router.push({
      name:'HeroLists'
    })
  }
  private created(): void {
    //刷新页面后获取$route中的params 会变成string类型 需要Number强转
    this.id = Number(this.$route.params.id);
    this.heroName = this.$store.state.HeroList.find(
      (v: { name: string; id: number }) => {
        return (v.id === Number(this.$route.params.id));
      }
    ).name;
  }
  // private beforeRouteEnter(to, from, next) {
  //   next(vm => {
  //     console.log(vm)
  //   });
  // }
}
